* {
  margin: 0;
  padding: 0; }

#navbar {
  background: linear-gradient(to bottom, #4a6b88, #0c0c0c); }

.logo {
  height: 10%;
  width: 10%; }

#jumbo {
  background: linear-gradient(to bottom, #54748f, #0c0c0c);
  color: #0c0c0c; }

h1 {
  font-family: 'Times New Roman', Times, serif;
  color: #fcfafa; }

h2 {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; }

h3 {
  color: white; }

.footer {
  background: linear-gradient(to bottom, #54748f, #0c0c0c); }

a span {
  text-decoration: none;
  color: #fcfafa; }

#copy {
  background-color: #525151;
  color: white; }
